/* 굿로드 폰트 */
.sign-in-up-good-load {
  font-family: Fredoka One;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 60px;
  letter-spacing: 0.01em;
  color: #ffb300;
}
/* 이메일 비번 배경 */
.signin-input {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px 10px 2px 10px;
  margin: 10px 20px 10px 20px;
  height: 58px;
  background: #ffefcb;
  border-radius: 30px;
}
/* 가로라인  */
.sign-line {
  margin: 5px 20px 5px 20px;
  height: 1px;
  background: rgba(0, 0, 0, 0.15);
  /* border: 1px solid rgba(0, 0, 0, 0.15); */
}
/* 업로드 외곽라인 */
.signup-upload {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  padding: 20px 20px 20px 20px;
  background: #ffffff;
  border: 1px solid #999999;
  box-sizing: border-box;
  border-radius: 50px;
  font-family: "Elice Digital Baeum", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
}
