html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.common-bg {
  background-image: url("../img/main_bg_image2.svg"),
    url("../img/main_bg_image.svg");
  background-size: 1920px 1371px, 1920px 815px;
  background-position: 0px 900px, 0 0;
  background-repeat: no-repeat;
}
.common-bg-login {
  background-image: url("../img/login_bg.svg");
  background-repeat: no-repeat;
  background-size: 1920px;
  background-position: 0 0;
}
.common-bg-myinfo {
  background-image: url("../img/treatment_bg.svg");
  background-repeat: no-repeat;
}
.common-bg-group-add {
  background-image: url("../img/new_classify_bg.svg");
  background-repeat: no-repeat;
}

